<template>
    <!---->
    <div v-loading="loading">
        <div class="p-lg appli-container">
            <sub-header :list="subNavList"></sub-header>
            <div class="page-container">
                <div class="page-toolbar clear m-t-sm">
                    <search-ipts :options="searchOptions" @submit="doSearch" v-show="searchShow"></search-ipts>
                    <div class="pull-left toolbar-candle clear">
                        <div class="app-refresh btn bg-gray1" title="" @click="refresh"><i
                            class="fa fa-refresh"></i></div>
                    </div>
                    <div class="pull-right clear">
                        <div class="pull-left m-r-sm opacity-8" title="">
                            <select-checkbox :list="showList" v-model="selectVal" style="width: 60px;"></select-checkbox>
                        </div>
                        <div class="pull-left btn opacity-8 search-btn" @click="searchShow = !searchShow"><i
                            class="fa fa-search" title=""></i></div>
                    </div>
                </div>
                <div class="page-contaoner">
                    <div class="lk-table m-t-sm">
                        <ul class="table-thead clear">
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1" style="max-width: 60px;"></li>
                            <li class="col-xs-2 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-2 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-4 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-2 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1" style="min-width: 130px;"></li>
                        </ul>
                        <ul class="table-tbody clear" v-for="(item, index) in data.items">
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1" style="max-width: 60px;">{{offset + index + 1}}</li>
                            <li class="col-xs-2 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.settlement_code">{{item.settlement_code}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.agent_name">{{item.agent_name}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.apply_month">{{item.apply_month}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.settlement_money">{{item.settlement_money}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.status_name"
                                :class="{'text-999': item.status_name==''}">{{item.status_name}}</li>
                            <li class="col-xs-2 p-n over-omit" v-show="selectVal.indexOf('')!=-1":title="item.apply_time">{{item.apply_time}}</li>
                            <li class="col-xs-4 p-n over-omit text-left" v-show="selectVal.indexOf('')!=-1" style="padding:2px 5px;"
                                :title="item.account_name + ',' + item.bank_name + ',' + item.bank_account + ',' + item.open_bank">
                                <div class="over-omit" style="line-height: 18px;font-size: 12px;">{{item.account_name}}, {{item.bank_name}}</div>
                                <div class="over-omit" style="line-height: 18px;font-size: 12px;">{{item.bank_account}}, {{item.open_bank}}</div>
                            </li>
                            <li class="col-xs-2 p-n over-omit" v-show="selectVal.indexOf('')!=-1":title="item.settlement_time">{{item.settlement_time}}</li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1" style="min-width: 130px;">
                                <a href="javascript:;" class="link" :class="{'disabled': !(item.status_name==''&&item.certificate)}" @click.stop="doClearing(item)"></a>
                                <router-link :to="{name: 'clearingDetail', params: {id: item.settlement_code}, query: {apply_month: item.apply_month}}" href="javascript:;" class="link"></router-link>
                                <a :href="item.certificate" class="link" @click.stop target="_blank" v-if="item.certificate"></a>
                                <el-upload
                                    v-else
                                    class="inline-block"
                                    :action="uploadUrl"
                                    accept="image/*"
                                    :show-file-list="false"
                                    :headers="header"
                                    :on-success="uploadCertificate">
                                    <a href="javascript:;" title="" class="link"></a>
                                </el-upload>
                            </li>
                        </ul>
                        <ul class="table-tbody clear" v-if="data.items.length===0">
                            <li class="p-n over-omit"></li>
                        </ul>
                    </div>
                    <div class="footer clear m-t-md">
                        <div class="pull-left news-record text-lg">
                            <span v-show="data.items.length>0">{{offset + 1}}{{offset + data.items.length}}</span><span
                            class="text-blue">{{data.total}}</span> 
                            <el-select v-model="limit" placeholder="" style="width: 66px;">
                                <el-option
                                    v-for="(item,index) in options"
                                    :key="index"
                                    :label="item"
                                    :value="item">
                                </el-option>
                            </el-select>
                            
                        </div>
                        <div class="pull-right">
                            <el-pagination
                                background
                                v-if="data.items.length>0"
                                layout="prev, pager, next"
                                :current-page.sync="page"
                                :page-count="pages">
                            </el-pagination>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
    import SubHeader from '../common/subheader'
    import api from '@/api'
    import SelectCheckbox from '@/components/SelectCheckbox'
    import SearchIpts from '../common/searchIpts'
    export default {
        //
        components: {
            SelectCheckbox,
            SearchIpts,
            SubHeader
        },
        data: () => ({
            data: {
                limit: 10,
                page: 1,
                pageSize: 1,
                total: 1,
                items: []
            },
            loading: false,
            selectVal: ['', '', '', '', '', '', '', '', '', ''],
            showList: ['', '', '', '', '', '', '', '', '', ''],
            options: [10, 25, 50],   //
            searchShow: false,   //
            limit: 10,
            page: 1,
            header: {ContentType: 'application/x-www-form-urlencoded'},
            subNavList: {
                parentNode: {
                    name: '',
                    router: {
                        name: 'financial_statistics'
                    }
                },
                childNode: {
                    name: '',
                    desc: '',
                    router: {
                        name: 'financial_clearing'
                    }
                }
            },
            //
            searchOptions: [
                {
                    type: 'text',
                    name: '',
                    value: ''
                },
                {
                    type: 'text',
                    name: '',
                    value: ''
                },
                {
                    type: 'time',
                    name: '',
                    value: ''
                },
                {
                    type: 'time',
                    name: '',
                    value: ''
                },
                {
                    type: 'select',
                    name: '',
                    value: '',
                    options: [
                        {
                            label: '',
                            value: 1
                        },
                        {
                            label: '',
                            value: 2
                        }
                    ]
                }
            ]
        }),
        computed: {
            //
            pages () {
                return this.data.pageSize || 1
            },
            offset () {
                return (this.page - 1) * this.limit
            },
            uploadUrl () {
                return api.financial.uploadCertificate
            }
        },
        methods: {
            //
            getList () {
                this.loading = true
                this.$http.post(api.financial.clearing, {
                    settlement_code: this.searchOptions[0].value,
                    agent_name: this.searchOptions[1].value,
                    start_time: this.searchOptions[2].value,
                    end_time: this.searchOptions[3].value,
                    status: this.searchOptions[4].value,
                    page: this.page,
                    limit: this.limit
                }).then(res => {
                    this.selectedGroup = []
                    let that = this
                    setTimeout(function () {
                        that.loading = false
                    }, 500)
                    if (res.data.code === 1) {
                        this.data = res.data.data
                    } else {
                        this.data = {
                            limit: 10,
                            page: 1,
                            pageSize: 1,
                            total: 1,
                            items: []
                        }
                        this.$message({
                            type: 'warning',
                            message: res.data.msg
                        })
                    }
                })
            },
            doClearing (item) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$http.get(api.financial.clearingComplete, {
                        params: {
                            id: item.id
                        }
                    }).then(res => {
                        if (res.data.code === 1) {
                            this.$message({
                                type: 'success',
                                message: '!'
                            })
                            this.getList()
                        } else {
                            this.$message({
                                type: 'warning',
                                message: res.data.msg
                            })
                        }
                    })
                })
            },
            uploadCertificate () {
                if (res.code === 1) {
                    this.$message({
                        type: 'success',
                        message: ''
                    })
                    this.getList()
                } else {
                    this.$message({
                        type: 'error',
                        message: res.msg
                    })
                }
            },
            //
            refresh () {
                this.getList()    //
            },
            // 
            doSearch (data) {
                this.page = 1
                this.searchOptions = data
                this.getList()
            },
            // 
            addPage () {
                if (this.page < this.pages) this.page += 1
            },
            // 
            delPage () {
                if (this.page > 1) this.page -= 1
            }
        },
        created () {
            this.page = this.$route.query.page ? parseInt(this.$route.query.page) : 1
            this.getList()
        },
        watch: {
            page (val) {
                this.$router.replace({name: 'financial_clearing', query: {page: val}})
                this.getList()
            },
            limit (val) {
                this.getList()
            }
        }
    }
</script>
<style scoped>
</style>



