<template>
    <!---->
    <div v-loading="loading">
        <div class="p-lg appli-container">
            <sub-header :list="subNavList"></sub-header>
            <div class="page-container">
                <div class="page-toolbar clear m-t-sm">
                    <search-ipts :options="searchOptions" @submit="doSearch" v-show="searchShow"></search-ipts>
                    <h5 class="text-center text-bold text-xxlg">{{searchOptions[0].value}}{{searchOptions[1].value}}</h5>
                </div>
                <div class="page-contaoner">
                    <div class="lk-table m-t-sm">
                        <ul class="table-thead clear">
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('5%')!=-1">5%</li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('3.3%')!=-1">3.3%</li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                        </ul>
                        <ul class="table-tbody clear" v-for="(item, index) in data">
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1" :title="item.date">{{item.date}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.ht_profit">{{item.ht_profit}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.xg">{{item.xg}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.agent">{{item.agent}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.cinema">{{item.cinema}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1":title="item.copyright">{{item.copyright}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('5%')!=-1":title="item.special_funds">{{item.special_funds}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('3.3%')!=-1":title="item.sales_tax">{{item.sales_tax}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1":title="item.subtotal">{{item.subtotal}}</li>
                        </ul>
                        <ul class="table-tbody clear" v-if="data.length===0">
                            <li class="p-n over-omit"></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
    import SubHeader from '../common/subheader'
    import api from '@/api'
    import SelectCheckbox from '@/components/SelectCheckbox'
    import SearchIpts from '../common/searchIpts'
    export default {
        //
        components: {
            SelectCheckbox,
            SearchIpts,
            SubHeader
        },
        data: () => ({
            data: [],
            yearList: [],
            monthList: [],
            loading: false,
            selectVal: ['', '', '', '', '', '', '5%', '3.3%', ''],
            showList: ['', '', '', '', '', '', '5%', '3.3%', ''],
            options: [10, 25, 50],   //
            searchShow: true,   //
            limit: 10,
            page: 1,
            subNavList: {
                parentNode: {
                    name: '',
                    router: {
                        name: 'financial_statistics'
                    }
                },
                childNode: {
                    name: '',
                    desc: '',
                    router: {
                        name: 'financial_report'
                    }
                }
            },
            //
            searchOptions: [
                {
                    type: 'select',
                    name: '',
                    value: '',
                    options: []
                },
                {
                    type: 'select',
                    name: '',
                    value: '',
                    options: []
                }
            ],
        }),
        methods: {
            //
            getList () {
                this.loading = true
                this.$http.post(api.financial.report, {
                    year: this.searchOptions[0].value,
                    month: this.searchOptions[1].value,
                    page: this.page,
                    limit: this.limit
                }).then(res => {
                    let that = this
                    setTimeout(function () {
                        that.loading = false
                    }, 500)
                    if (res.data.code === 1) {
                        this.data = res.data.data
                    } else {
                        this.data = []
                        this.$message({
                            type: 'warning',
                            message: res.data.msg
                        })
                    }
                })
            },
            getOptions () {
                let year = new Date().getFullYear()
                let month = new Date().getMonth() + 1
                for (let i = 2000; i <= year; i++) {
                    this.searchOptions[0].options.push({
                        label: i + '',
                        value: i
                    })
                }
                for (let i = 1; i < 13; i ++) {
                    this.searchOptions[1].options.push({
                        label: i + '',
                        value: i
                    })
                }
                this.searchOptions[0].value = year
                this.searchOptions[1].value = month
            },
            //
            refresh () {
                this.getList()    //
            },
            // 
            doSearch (data) {
                this.page = 1
                this.searchOptions = data
                this.getList()
            },
            // 
            addPage () {
                if (this.page < this.pages) this.page += 1
            },
            // 
            delPage () {
                if (this.page > 1) this.page -= 1
            }
        },
        created () {
            this.getOptions()
            this.page = this.$route.query.page ? parseInt(this.$route.query.page) : 1
            this.getList()
        },
        watch: {
            page (val) {
                this.$router.replace({name: 'financial_report', query: {page: val}})
                this.getList()
            },
            limit (val) {
                this.getList()
            }
        }
    }
</script>
<style scoped>
</style>



