<template>
    <div v-loading="loading">
        <div class="p-lg">
            <sub-header :list="subNavList"></sub-header>
            <div class="page-container">
                <div class="lk-table m-t-sm">
                    <ul class="table-thead clear">
                        <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                        <li class="col-xs-2 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                        <li class="col-xs-2 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                        <!--<li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1" style="min-width: 130px;"></li>-->
                    </ul>
                    <ul class="table-tbody clear" v-for="(item, index) in data">
                        <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1">{{index + 1}}</li>
                        <li class="col-xs-2 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.name">{{item.name}}</li>
                        <li class="col-xs-2 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.proportion">{{item.proportion}}%</li>
                        <!--<li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1" style="min-width: 130px;">-->
                            <!--<a href="javascript:;" class="link" @click.stop="openAccount(item)"></a>-->
                            <!--<a href="javascript:;" class="link" @click.stop="delItem(item.id)"></a>-->
                        <!--</li>-->
                    </ul>
                </div>
                <el-dialog
                    title=""
                    :visible.sync="accountModal"
                    custom-class="dialog-modal2"
                    :close-on-click-modal="false">
                    <div class="text-center m-t-lg clear">
                        <div class="clear m-b-sm">
                            <div class="col-xs-12 col-md-2 p-v-sm p-r-n text-right min-width-105">:</div>
                            <div class="col-xs-11 col-md-6">
                                <el-input placeholder="" v-model="accountNum"></el-input>
                            </div>
                            <div class="col-xs-1 col-md-1 p-n text-left line-height-40">
                                %
                            </div>
                        </div>
                        <div class="col-xs-12 col-md-9 m-t-lg">
                            <el-button type="primary" @click="setAccount"> </el-button>
                            <el-button @click="accountModal = false"> </el-button>
                        </div>
                    </div>
                </el-dialog>
            </div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
    import SubHeader from '../common/subheader'
    import api from '@/api'
    import SelectCheckbox from '@/components/SelectCheckbox'
    import SearchIpts from '../common/searchIpts'
    export default {
        //
        components: {
            SelectCheckbox,
            SearchIpts,
            SubHeader
        },
        data: () => ({
            data: [],
            accountModal: false,
            accountNum: '',
            accountItem: {},
            loading: false,
            selectVal: ['', '', ''],
            showList: ['', '', ''],
            subNavList: {
                parentNode: {
                    name: '',
                    router: {
                        name: 'financial_statistics'
                    }
                },
                childNode: {
                    name: '',
                    desc: '',
                    router: {
                        name: 'financial_routing'
                    }
                }
            },
        }),
        methods: {
            getList () {
                this.$http.get(api.financial.routing).then(res => {
                    if (res.data.code === 1) {
                        this.data = res.data.data
                    } else {
                        this.data = []
                        this.$message.error(res.data.msg)
                    }
                })
            },
            openAccount (item) {
                this.accountNum = item.proportion || 0
                this.accountItem = item
                this.accountModal = true
            },
            setAccount () {
                if (this.accountNum) {
                    if (!/^100$|^(\d|[1-9]\d)(\.\d{1,2})*$/.test(this.accountNum)) {
                        this.$message({
                            type: 'warning',
                            message: '100,'
                        })
                        return
                    }
                    this.$http.post(api.financial.setAccount, {
                        proportion: this.accountNum,
                        id: this.accountItem.id
                    }).then(res => {
                        if (res.data.code === 1) {
                            this.$message({
                                type: 'success',
                                message: ''
                            })
                            this.getList()
                            this.accountModal = false
                        } else {
                            this.$message({
                                type: 'error',
                                message: res.data.msg
                            })
                        }
                    })
                }  else {
                    this.$message({
                        type: 'warning',
                        message: ''
                    })
                }
            },
            //
            delItem(id) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$http.get(api.financial.routingDel, {
                        params: {
                            id: id
                        }
                    }).then(res => {
                        if (res.data.code === 1) {
                            this.$message({
                                type: 'success',
                                message: '!'
                            })
                            this.getList()
                        } else {
                            this.$message({
                                type: 'warning',
                                message: res.data.msg
                            })
                        }
                    })
                }).catch(() => {
                })
            }
        },
        created () {
            this.getList()
        },
        watch: {
        }
    }
</script>
<style scoped>
</style>



