<template>
    <!---->
    <div v-loading="loading">
        <div class="p-lg appli-container">
            <sub-header :list="subNavList"></sub-header>
            <div class="page-container">
                <div class="page-toolbar clear m-t-sm">
                    <search-ipts :options="searchOptions" @submit="doSearch" v-show="searchShow"></search-ipts>
                    <div class="pull-left toolbar-candle clear">
                        <router-link :to="{name: 'agent_add'}" href="javascript:;" title="" class="app-add btn bg-blue1 text-white"><i class="fa fa-plus-square"></i></router-link>
                        <div class="app-refresh btn bg-gray1" title="" @click="refresh"><i
                            class="fa fa-refresh"></i></div>
                    </div>
                    <div class="pull-right clear">
                        <div class="pull-left m-r-sm opacity-8" title="">
                            <select-checkbox :list="showList" v-model="selectVal" style="width: 60px;"></select-checkbox>
                        </div>
                        <div class="pull-left btn opacity-8 search-btn" @click="searchShow = !searchShow"><i
                            class="fa fa-search" title=""></i></div>
                    </div>
                </div>
                <div class="page-contaoner">
                    <div class="lk-table m-t-sm">
                        <ul class="table-thead clear">
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1" style="max-width: 60px;"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-24 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-24 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-24 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-24 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-24 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-24 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1" style="min-width: 130px;"></li>
                        </ul>
                        <ul class="table-tbody clear" v-for="(item, index) in data.items">
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1" style="max-width: 60px;">{{offset + index + 1}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.name">{{item.code}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.name">{{item.name}}</li>
                            <li class="col-xs-24 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.region_name">{{item.region_name}}</li>
                            <li class="col-xs-24 p-n over-omit" v-show="selectVal.indexOf('')!=-1":title="item.cinema_num">{{item.cinema_num}}</li>
                            <li class="col-xs-24 p-n over-omit" v-show="selectVal.indexOf('')!=-1":title="item.hall_num">{{item.hall_num}}</li>
                            <li class="col-xs-24 p-n over-omit" v-show="selectVal.indexOf('')!=-1":title="item.device_num">{{item.device_num}}  </li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1":title="item.create_time">{{item.create_time}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1":title="item.contract_after_time">{{item.contract_after_time}}</li>
                            <li class="col-xs-24 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.status_name" :class="{'text-green':item.status==1, 'text-red':item.status==4, 'text-orange': item.status==2}">{{item.status_name}}</li>
                            <li class="col-xs-24 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.proportion">{{item.proportion}} %</li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1" style="min-width: 130px;">
                                <router-link :to="{name: 'agent_detail', params: {id: item.id}}" href="javascript:;" class="link" @click.stop></router-link>
                                <router-link :to="{name: 'agent_edit',params: {id: item.id}}" href="javascript:;" class="link" @click.stop></router-link>
                                <a href="javascript:;" :class="{'disabled': item.status!=1}" class="link" @click.stop="openAccount(item)"></a>
                            </li>
                        </ul>
                        <ul class="table-tbody clear" v-if="data.items.length===0">
                            <li class="p-n over-omit"></li>
                        </ul>
                    </div>
                    <div class="footer clear m-t-md">
                        <div class="pull-left news-record text-lg">
                            <span v-show="data.items.length>0">{{offset + 1}}{{offset + data.items.length}}</span><span
                            class="text-blue">{{data.total}}</span> 
                            <el-select v-model="limit" placeholder="" style="width: 66px;">
                                <el-option
                                    v-for="(item,index) in options"
                                    :key="index"
                                    :label="item"
                                    :value="item">
                                </el-option>
                            </el-select>
                            
                        </div>
                        <div class="pull-right">
                            <el-pagination
                                v-if="data.items.length>0"
                                background
                                layout="prev, pager, next"
                                :current-page.sync="page"
                                :page-count="pages">
                            </el-pagination>
                        </div>
                    </div>
                    <el-dialog
                        title=""
                        :visible.sync="accountModal"
                        custom-class="dialog-modal2"
                        :close-on-click-modal="false">
                        <div class="text-center m-t-lg clear">
                            <div class="clear m-b-sm">
                                <div class="col-xs-12 col-md-2 p-v-sm p-r-n text-right min-width-105">:</div>
                                <div class="col-xs-11 col-md-6">
                                    <el-input placeholder="" v-model="accountNum"></el-input>
                                </div>
                                <div class="col-xs-1 col-md-1 p-n text-left line-height-40">
                                    %
                                </div>
                            </div>
                            <div class="col-xs-12 col-md-9 m-t-lg">
                                <el-button type="primary" @click="setAccount"> </el-button>
                                <el-button @click="accountModal = false"> </el-button>
                            </div>
                        </div>
                    </el-dialog>
                </div>
            </div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
    import SubHeader from '../common/subheader'
    import api from '@/api'
    import SelectCheckbox from '@/components/SelectCheckbox'
    import SearchIpts from '../common/searchIpts'
    export default {
        //
        components: {
            SelectCheckbox,
            SearchIpts,
            SubHeader
        },
        data: () => ({
            data: {
                limit: 10,
                page: 1,
                pageSize: 1,
                total: 1,
                items: []
            },
            accountModal: false,
            accountNum: '',
            accountItem: {},
            loading: false,
            selectVal: ['', '', '', '', '', '', '', '', '', '','', ''],
            showList: ['', '', '', '', '', '', '', '', '', '','', ''],
            options: [10, 25, 50],   //
            searchShow: false,   //
            limit: 10,
            page: 1,
            subNavList: {
                parentNode: {
                    name: '',
                    router: {
                        name: 'agent_list'
                    }
                },
                childNode: {
                    name: '',
                    desc: '',
                    router: {
                        name: 'agent_mine'
                    }
                }
            },
            //
            searchOptions: [
                {
                    type: 'text',
                    name: '',
                    value: ''
                },
                {
                    type: 'time',
                    name: '',
                    value: ''
                },
                {
                    type: 'time',
                    name: '',
                    value: ''
                },
                {
                    type: 'searchSelect',
                    name: '',
                    value: '',
                    options:[]
                },
                {
                    type: 'select',
                    name: '',
                    value: '',
                    options: [
                        {
                            value:'1',
                            label:''
                        },
                        {
                            value:'2',
                            label:''
                        },
                        {
                            value:'4',
                            label:''
                        },

                    ]
                },
            ],
        }),
        computed: {
            //
            pages () {
                return this.data.pageSize || 1
            },
            offset () {
                return (this.page - 1) * this.limit
            }
        },
        methods: {
            //
            getList () {
                this.loading = true
                this.$http.post(api.agent.list, {
                    name: this.searchOptions[0].value,
                    start_time: this.searchOptions[1].value,
                    end_time: this.searchOptions[2].value,
                    region_id: this.searchOptions[3].value,
                    status: this.searchOptions[4].value,
                    type: 2,
                    page: this.page,
                    limit: this.limit
                }).then(res => {
                    this.selectedGroup = []
                    let that = this
                    setTimeout(function () {
                        that.loading = false
                    }, 500)
                    if (res.data.code === 1) {
                        this.data = res.data.data
                    } else {
                        this.data = {
                            limit: 10,
                            page: 1,
                            pageSize: 1,
                            total: 1,
                            items: []
                        }
                        this.$message({
                            type: 'warning',
                            message: res.data.msg
                        })
                    }
                })
            },
            openAccount (item) {
                this.accountNum = item.proportion || 0
                this.accountItem = item
                this.accountModal = true
            },
            setAccount () {
                if (this.accountNum) {
                    if (!/^100$|^(\d|[1-9]\d)(\.\d{1,2})*$/.test(this.accountNum)) {
                        this.$message({
                            type: 'warning',
                            message: '100,'
                        })
                        return
                    }
                    this.$http.post(api.agent.setAccount, {
                        proportion: this.accountNum,
                        agent_id: this.accountItem.id
                    }).then(res => {
                        if (res.data.code === 1) {
                            this.$message({
                                type: 'success',
                                message: ''
                            })
                            this.getList()
                            this.accountModal = false
                        } else {
                            this.$message({
                                type: 'error',
                                message: res.data.msg
                            })
                        }
                    })
                }  else {
                    this.$message({
                        type: 'warning',
                        message: ''
                    })
                }
            },
            //
            refresh () {
                this.getList()    //
            },
            // 
            doSearch (data) {
                this.page = 1
                this.searchOptions = data
                this.getList()
            },
            // 
            addPage () {
                if (this.page < this.pages) this.page += 1
            },
            // 
            delPage () {
                if (this.page > 1) this.page -= 1
            }
        },
        created () {
            this.page = this.$route.query.page ? parseInt(this.$route.query.page) : 1
            this.getList()
        },
        watch: {
            page (val) {
                this.$router.replace({name: 'agent_mine', query: {page: val}})
                this.getList()
            },
            limit (val) {
                this.getList()
            },
            searchShow (searchShow){
                this.$http.post(api.common.getCity, {
                    status: 1
                }).then(res => {
                    if (res.data.code === 1) {
                        this.searchOptions[3].options = res.data.data.map(val => {
                            return {
                                label: val.name,
                                value: val.id
                            }
                        })
                        //                        this.region_id = this.regionList[0].value
                    }
                    //                    else {
                    //                        this.regionList = []
                    //                    }
                })
            },
        }
    }
</script>
<style scoped>
</style>



