<template>
    <!---->
    <div v-loading="loading">
        <div class="p-lg appli-container">
            <sub-header :list="subNavList"></sub-header>
            <div class="page-container">
                <div class="page-toolbar clear m-t-sm">
                    <search-ipts :options="searchOptions" @submit="doSearch" v-show="searchShow"></search-ipts>
                    <div class="pull-left toolbar-candle clear">
                        <div class="app-refresh btn bg-gray1" title="" @click="refresh"><i
                            class="fa fa-refresh"></i></div>
                    </div>
                    <div class="pull-right clear">
                        <div class="pull-left m-r-sm opacity-8" title="">
                            <select-checkbox :list="showList" v-model="selectVal" style="width: 60px;"></select-checkbox>
                        </div>
                        <div class="pull-left btn opacity-8 search-btn" @click="searchShow = !searchShow"><i
                            class="fa fa-search" title=""></i></div>
                    </div>
                </div>
                <div class="page-contaoner">
                    <div class="lk-table m-t-sm">
                        <ul class="table-thead clear">
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1" style="max-width: 60px;"></li>
                            <li class="col-xs-2 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-2 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                        </ul>
                        <ul class="table-tbody clear" v-for="(item, index) in data.items">
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1" style="max-width: 60px;">{{offset + index + 1}}</li>
                            <li class="col-xs-2 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.trade_id">{{item.trade_id}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.buyer">{{item.buyer}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.city_name">{{item.city_name}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.film_name">{{item.film_name}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.cinema_name">{{item.cinema_name}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1":title="item.pay_type">{{item.pay_type}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1":title="item.original_price">{{item.original_price}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1":title="item.pay_money">{{item.pay_money}}</li>
                            <li class="col-xs-2 p-n over-omit" v-show="selectVal.indexOf('')!=-1":title="item.create_time">{{item.create_time}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.status_name"
                                :class="{'text-green': item.status_name=='','text-orange': item.status_name=='','text-red': item.status_name==''||item.status_name==''}">{{item.status_name}}</li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1">
                                <a href="javascript:;" class="link" @click.stop="lookDetail(item)"></a>
                            </li>
                        </ul>
                        <ul class="table-tbody clear" v-if="data.items.length===0">
                            <li class="p-n over-omit"></li>
                        </ul>
                    </div>
                    <div class="footer clear m-t-md">
                        <div class="pull-left news-record text-lg">
                            <span v-show="data.items.length>0">{{offset + 1}}{{offset + data.items.length}}</span><span
                            class="text-blue">{{data.total}}</span> 
                            <el-select v-model="limit" placeholder="" style="width: 66px;">
                                <el-option
                                    v-for="(item,index) in options"
                                    :key="index"
                                    :label="item"
                                    :value="item">
                                </el-option>
                            </el-select>
                            
                        </div>
                        <div class="pull-right">
                            <el-pagination
                                background
                                v-if="data.items.length>0"
                                layout="prev, pager, next"
                                :current-page.sync="page"
                                :page-count="pages">
                            </el-pagination>
                        </div>
                    </div>
                    <el-dialog
                        title=""
                        :visible.sync="detailModal"
                        custom-class="dialog-modal1">
                        <div v-loading="modalLoading">
                            <div class="clear">
                                <div class="col-xs-2 line-height-40 text-right text-bold min-width-105"></div>
                                <div class="col-xs-8 line-height-40 text-bold"></div>
                            </div>
                            <div class="clear bg-f9">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.trade_id}}</div>
                            </div>
                            <div class="clear">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.city_name}}</div>
                            </div>
                            <div class="clear bg-f9">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.agent_name}}</div>
                            </div>
                            <div class="clear">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.agent_code}}</div>
                            </div>
                            <div class="clear bg-f9">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.buyer}}</div>
                            </div>
                            <div class="clear">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.film_name}}</div>
                            </div>
                            <div class="clear bg-f9">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.cinema_name}}</div>
                            </div>
                            <div class="clear">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.cinema_code}}</div>
                            </div>
                            <div class="clear bg-f9">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.original_price}}</div>
                            </div>
                            <div class="clear">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.discount_money}}</div>
                            </div>
                            <div class="clear bg-f9">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.pay_money}}</div>
                            </div>
                            <div class="clear">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.pay_type}}</div>
                            </div>
                            <div class="clear bg-f9">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.create_time}}</div>
                            </div>
                            <div class="clear">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.pay_time}}</div>
                            </div>
                            <div class="clear bg-f9">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.watch_time}}</div>
                            </div>
                            <div class="clear">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.status}}</div>
                            </div>
                        </div>
                    </el-dialog>
                </div>
            </div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
    import SubHeader from '../common/subheader'
    import api from '@/api'
    import SelectCheckbox from '@/components/SelectCheckbox'
    import SearchIpts from '../common/searchIpts'
    export default {
        //
        components: {
            SelectCheckbox,
            SearchIpts,
            SubHeader
        },
        data: () => ({
            data: {
                limit: 10,
                page: 1,
                pageSize: 1,
                total: 1,
                items: []
            },
            loading: false,
            modalLoading: false,
            detailModal: false,
            detailVal: {},
            selectVal: ['', '', '', '', '', '', '', '', '', '', '', ''],
            showList: ['', '', '', '', '', '', '', '', '', '', '', ''],
            options: [10, 25, 50],   //
            searchShow: false,   //
            limit: 10,
            page: 1,
            subNavList: {
                parentNode: {
                    name: '',
                    router: {
                        name: 'financial_statistics'
                    }
                },
                childNode: {
                    name: '',
                    desc: '',
                    router: {
                        name: 'financial_order'
                    }
                }
            },
            //
            searchOptions: [
                {
                    type: 'text',
                    name: '',
                    value: ''
                },
                {
                    type: 'text',
                    name: '',
                    value: ''
                },
                {
                    type: 'searchSelect',
                    name: '',
                    value: '',
                    options: []
                },
                {
                    type: 'select',
                    name: '',
                    value: '',
                    options: [
                        {
                            label: '',
                            value: 0
                        },
                        {
                            label: '',
                            value: 1
                        },
                        {
                            label: '',
                            value: 2
                        },
                        {
                            label: '',
                            value: 3
                        }
                    ]
                },
                {
                    type: 'time',
                    name: '',
                    value: ''
                },
                {
                    type: 'time',
                    name: '',
                    value: ''
                },
                {
                    type: 'text',
                    name: '',
                    value: ''
                },
                {
                    type: 'searchSelect',
                    name: '',
                    value: '',
                    options: []
                },
            ],
        }),
        computed: {
            //
            pages () {
                return this.data.pageSize || 1
            },
            offset () {
                return (this.page - 1) * this.limit
            }
        },
        methods: {
            //
            getList () {
                this.loading = true
                this.$http.post(api.financial.orderList, {
                    trade_id: this.searchOptions[0].value,
                    buyer: this.searchOptions[1].value,
                    cinema_id: this.searchOptions[2].value,
                    status: this.searchOptions[3].value,
                    start_time: this.searchOptions[4].value,
                    end_time: this.searchOptions[5].value,
                    film_name: this.searchOptions[6].value,
                    city_id: this.searchOptions[7].value,
                    page: this.page,
                    limit: this.limit
                }).then(res => {
                    this.selectedGroup = []
                    let that = this
                    setTimeout(function () {
                        that.loading = false
                    }, 500)
                    if (res.data.code === 1) {
                        this.data = res.data.data
                    } else {
                        this.data = {
                            limit: 10,
                            page: 1,
                            pageSize: 1,
                            total: 1,
                            items: []
                        }
                        this.$message({
                            type: 'warning',
                            message: res.data.msg
                        })
                    }
                })
            },
            //
            getcinemaList(){
                this.$http.post(api.cinema.getAllList, {
                    type_id: 1
                }).then(res => {
                    if (res.data.code === 1) {
                        this.searchOptions[2].options = res.data.data.map(val => {
                            return {
                                label: val.name,
                                value: val.id
                            }
                        })
                    }
                })
            },
            getCityList () {
                this.$http.post(api.common.getCity, {
                    status: 3
                }).then(res => {
                    if (res.data.code === 1) {
                        this.searchOptions[7].options = res.data.data.map(val => {
                            return {
                                label: val.name,
                                value: val.id
                            }
                        })
                    }
                })
            },
            getDetailData (id) {
                this.modalLoading = true
                this.$http.post(api.financial.orderDetail, {
                    trade_id: id
                }).then(res => {
                    let that = this
                    setTimeout(function () {
                        if (res.data.code === 1) {
                            that.modalLoading = false
                            that.detailVal = res.data.data
                        }
                    }, 500)
                })
            },
            lookDetail (item) {
                this.detailModal = true
                this.getDetailData(item.trade_id)
            },
            //
            refresh () {
                this.getList()    //
            },
            // 
            doSearch (data) {
                this.page = 1
                this.searchOptions = data
                this.getList()
            },
            // 
            addPage () {
                if (this.page < this.pages) this.page += 1
            },
            // 
            delPage () {
                if (this.page > 1) this.page -= 1
            }
        },
        created () {
            this.page = this.$route.query.page ? parseInt(this.$route.query.page) : 1
            this.getList()
        },
        watch: {
            page (val) {
                this.$router.replace({name: 'financial_order', query: {page: val}})
                this.getList()
            },
            limit (val) {
                this.getList()
            },
            searchShow (searchShow){
                this.getcinemaList()
                this.getCityList()
            },
            detailModal (val) {
                if (!val) {
                    document.getElementsByClassName('el-dialog__body')[0].scrollTo(0, 0)
                }
            }
        }
    }
</script>
<style scoped>
</style>



